// Colors
$gray: #eee;
$pink: #ff69b4;
$black: #000;
// Sizes
$cell-size: 100px;
$border-size: 1px;
$font-size: 30px;
// Calculation variables
$calendar-width: (7 * $cell-size) + (16 * $border-size) ;
$column-count: 7;
$start-days: (
  "mon" 0,
  "tue" 102px,
  "wed" 203px,
  "thu" 303px,
  "fri" 405px,
  "sat" 506px,
  "sun" 607px
);
